body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #151723;
  color: white;
}

@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@700&display=swap');


@import "https://cdn.jsdelivr.net/npm/bootstrap@5.3.3/dist/css/bootstrap.min.css";
/* Firefox (uncomment to work in Firefox, although other properties will not work!)  */
/** {
  scrollbar-width: thin;
  scrollbar-color: #E40046 #161A42;
}*/

/* Chrome, Edge and Safari */
*::-webkit-scrollbar {
  height: 14px;
  width: 14px;
}
*::-webkit-scrollbar-track {
  border-radius: 8px;
  background-color: #161A42;
}

*::-webkit-scrollbar-track:hover {
  background-color: #2C3485;
}

*::-webkit-scrollbar-track:active {
  background-color: #161A42;
}

*::-webkit-scrollbar-thumb {
  border-radius: 9px;
  background-color: #E40046;
}

*::-webkit-scrollbar-thumb:hover {
  background-color: #B00036;
}

*::-webkit-scrollbar-thumb:active {
  background-color: #E40046;
}

h1{
  font-family: "League Spartan", sans-serif;
  letter-spacing: -5px;
}
/* #e40046 */
.gradient {
  background: rgb(22, 26, 66);
  background: -moz-linear-gradient(38deg, rgba(22, 26, 66, 1) 0%, rgba(22, 26, 66, 1) 53%, rgba(228, 0, 70, 1) 100%);
  background: -webkit-linear-gradient(38deg, rgba(22, 26, 66, 1) 0%, rgba(22, 26, 66, 1) 53%, rgba(228, 0, 70, 1) 100%);
  background: linear-gradient(38deg, rgba(22, 26, 66, 1) 0%, rgba(22, 26, 66, 1) 53%, rgba(228, 0, 70, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#161a42", endColorstr="#e40046", GradientType=1);
}

.gradi {
  width: 100%;
  min-height: 100%;
  height: auto;

  background: rgb(22, 26, 66);
  background: -moz-linear-gradient(0deg, rgba(22, 26, 66, 1) 44%, rgba(228, 0, 70, 0) 100%);
  background: -webkit-linear-gradient(0deg, rgba(22, 26, 66, 1) 44%, rgba(228, 0, 70, 0) 100%);
  background: linear-gradient(0deg, rgba(22, 26, 66, 1) 44%, rgba(228, 0, 70, 0) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#161a42", endColorstr="#e40046", GradientType=1);
}

.video-container {
  position: relative;
  width: 100%;
  max-width: 100%;
  min-height: 100%;
  padding: 0px;
  margin: 0px;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 99%;
  padding: 10px;
  color: white;
  font-size: 24px;
}

video {
  width: 100%;
  height: 100%;
}

.base {
  opacity: 0;
  animation: fadeIn 1s forwards;
  padding: 50px 50px 0px 50px;
}

.fadein {
  opacity: 0;
  animation: fadeIn 1s forwards;

}

@keyframes fadeIn {
  to {
    opacity: 1;
    padding: 0px;

  }
}
*:focus {
  outline: none;
  border: 3px solid red;
}
