#box {
  margin:20px 4px 20px 4px;
  width: 200px;
  height: 150px;
  min-width: 199px;
  min-height: 149px;
  border-radius: 20px;
  
  -webkit-box-shadow: 1px 1px 70px -16px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 1px 1px 70px -16px rgba(0, 0, 0, 0.75);
  box-shadow: 1px 1px 70px -16px rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid #161a42;
}
#box-config {
  margin:20px 4px 20px 24px;
  width: 100px;
  height: 150px;
  border-radius: 20px;
  background-color: #ff034f;
  -webkit-box-shadow: 1px 1px 70px -16px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 1px 1px 70px -16px rgba(0, 0, 0, 0.75);
  box-shadow: 1px 1px 70px -16px rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid #e40046;
  color: white;
}
